import type { NextPage, GetStaticPropsContext } from "next";
import Container from "@mui/material/Container";
import Box from "@mui/material/Box";
import { useTranslations } from "next-intl";

import ResponsiveAppBar from "../../src/components/ResponsiveAppBar";
import React from "react";
import Head from "next/head";
import Paper from "@mui/material/Paper";

import { useTheme } from "../../src/theme";
import SearchForm from "../../src/components/SearchForm";
import { Footer } from "../../src/components/Footer";
import Link from "../../src/components/Link";
import Breadcrumbs from "@mui/material/Breadcrumbs";
import { Typography } from "@mui/material";

const Airline: NextPage = ({ airline, otherAirlines }: any) => {
  const theme = useTheme();
  const t = useTranslations();

  return (
    <Box
      sx={{
        backgroundColor: theme.palette.mode === "light" ? "#F3F6F9" : "inherit",
      }}
    >
      <Head>
        <title>{`${airline.name} ${t("flights")} | Flightscanner`}</title>
      </Head>
      <ResponsiveAppBar />
      <Paper
        variant={theme.palette.mode === "light" ? "outlined" : "elevation"}
        sx={{ pb: 2, borderLeft: "none", borderRight: "none" }}
        square
      >
        <Typography
          variant="h3"
          gutterBottom
          sx={{ textAlign: "center", pt: 4 }}
        >
          {t("cheap-flights")} {airline.name}
        </Typography>
        <SearchForm maxWidth="lg" showBooking={false} />
      </Paper>

      <Container maxWidth="lg" sx={{ pt: 2 }}>
        <Breadcrumbs aria-label="breadcrumb">
          <Link underline="hover" color="inherit" href="/">
            Flightscanner
          </Link>
          <Link underline="hover" color="inherit" href={`/${t("routes-airlines")}`}>
            {t("airlines")}
          </Link>
          <Typography color="text.primary">{airline.name}</Typography>
        </Breadcrumbs>
      </Container>

      <Container maxWidth="lg" sx={{ py: 2 }}>
        <Paper sx={{ p: 2 }}>
          <Typography variant="h6" gutterBottom>
            {t("airlines")}
          </Typography>
          <Box
            component="ul"
            sx={{
              display: "flex",
              flexWrap: "wrap",
              listStyle: "none",
              p: 0,
            }}
          >
            {otherAirlines?.map((airline: any) => {
              const slug = airline.slug;
              const name = airline.name;
              return (
                <Box
                  key={name}
                  component="li"
                  sx={{ flex: { xs: "100%", sm: "50%", md: "0 0 33%" } }}
                >
                  <Link
                    sx={{ display: "block" }}
                    href={`/${t("routes-airline")}/${slug}`}
                    underline="hover"
                    color="inherit"
                  >
                    {name}
                  </Link>
                </Box>
              );
            })}
          </Box>
        </Paper>
      </Container>
      <Footer />
    </Box>
  );
};
export async function getStaticPaths() {
  // TODO should we generate build time?
  return {
    paths: [],
    fallback: true,
  };
}

export async function getStaticProps({
  locale,
  params,
  ...rest
}: GetStaticPropsContext) {
  const _airlines = require("../../data/Airlines.json");

  const slug = params?.id;

  const airline = _airlines.find(
    (airline: any) => airline.slug === String(slug).toLowerCase()
  );

  if (!airline) {
    return {
      notFound: true,
    };
  }

  const otherAirlines = _airlines.filter(
    (ar: any) => ar.country === airline.country && ar._id !== airline._id
  );

  return {
    props: {
      airline,
      otherAirlines,
      // You can get the messages from anywhere you like. The recommended
      // pattern is to put them in JSON files separated by language and read
      // the desired one based on the `locale` received from Next.js.
      messages: (await import(`../../messages/index/${locale || 'en'}.json`)).default,
    },
  };
}

export default Airline;
